import { types } from "../types/types";

interface State {
    checking: Boolean;
    uid?: String;
    name?: String;
}

interface Action {
    type: string;
    payload?: {
        uid: String;
        name: String;
    };
}
const initialState = {
    checking: false,
};

export const authReducer = (state: State = initialState, action: Action) => {
    switch (action.type) {
        case types.login:
            return { ...state, ...action.payload };
        case types.logout:
            return {
                checking: false,
            };
        case types.authCheckingFinish:
            return {
                checking: false,
            };
        default:
            return state;
    }
};
