import { Action, Dispatch } from "redux";
import Swal from "sweetalert2";
import { fetchNoToken, fetchWithToken } from "../helpers/fetch";
import { NewUser } from "../interfaces/NewUser";
import { User } from "../interfaces/User";
import { types } from "../types/types";

export const startLogin =
    (email: string, password: string) => async (dispatch: Dispatch<Action>) => {
        const resp = await fetchNoToken("auth", { email, password }, "POST");
        const body = await resp.json();

        if (body.ok) {
            localStorage.setItem("token", body.token);
            localStorage.setItem(
                "token-init-date",
                new Date().getTime().toString()
            );
            dispatch(login({ uid: body.uid, name: body.name }));
        } else {
            Swal.fire("Error", body.msg, "error");
        }
    };

export const startRegister =
    ({ email, name, password }: NewUser) =>
    async (dispatch: Dispatch<Action>) => {
        const resp = await fetchNoToken(
            "auth/new",
            { email, password, name },
            "POST"
        );
        const body = await resp.json();
        console.log(body);

        if (body.ok) {
            localStorage.setItem("token", body.token);
            localStorage.setItem(
                "token-init-date",
                new Date().getTime().toString()
            );
            dispatch(login({ uid: body.uid, name: body.name }));
        } else {
            Swal.fire("Error", body.errors[0].msg, "error");
        }
    };

export const startChecking = () => async (dispatch: Dispatch) => {
    const resp = await fetchWithToken("auth/renew", "");
    const body = await resp.json();
    if (body.ok) {
        localStorage.setItem("token", body.token);
        localStorage.setItem(
            "token-init-date",
            new Date().getTime().toString()
        );
        dispatch(
            login({
                uid: body.uid,
                name: body.name,
            })
        );
    } else {
        dispatch(checkingFinish());
    }
};

const checkingFinish = () => ({
    type: types.authCheckingFinish,
});

const login = (user: User) => ({
    type: types.login,
    payload: {
        uid: user.uid,
        name: user.name,
    },
});

export const startLogout = () => (dispatch: Dispatch) => {
    localStorage.clear();
    dispatch(logout());
};

const logout = () => ({
    type: types.logout,
});
