import { Dispatch } from "redux";
import Swal from "sweetalert2";
import { fetchWithToken } from "../helpers/fetch";
import { fileUpload } from "../helpers/file";
import { Note } from "../interfaces/Note";
import { RootState } from "../store/store";
import { types } from "../types/types";

export const startLoad = () => async (dispatch: Dispatch) => {
    try {
        const resp = await fetchWithToken("notes", "");
        const body = await resp.json();
        if (body.ok) {
            dispatch(loadNotes(body.notes));
        }
    } catch (error) {
        console.log(error);
    }
};

const loadNotes = (notes: Array<Note>) => ({
    type: types.notesLoad,
    payload: notes,
});

export const activeNote = (note: Note) => ({
    type: types.notesActive,
    payload: { ...note },
});

export const startNew =
    () => async (dispatch: Dispatch, getState: () => RootState) => {
        const uid = getState().auth.uid!.toString();
        const newNote: Note = {
            title: "",
            body: "",
            date: new Date().getTime(),
            url: "",
            id: "",
            uid,
        };

        try {
            const resp = await fetchWithToken("notes/new", newNote, "POST");
            const body = await resp.json();
            if (body.ok) {
                newNote.id = body.note.id;

                dispatch(activeNote(newNote));
                dispatch(addNew(newNote));
            }
        } catch (error) {
            console.log(error);
        }
    };

export const addNew = (note: Note) => ({
    type: types.notesAddNew,
    payload: note,
});

export const startDeleting =
    (id: string, note: Note) => async (dispatch: Dispatch) => {
        try {
            const resp = await fetchWithToken(`notes/${id}`, {}, "DELETE");
            const body = await resp.json();
            if (body.ok) {
                Swal.fire({
                    title: "Deleted",
                    text: "Your note has been deleted",
                    icon: "success",
                    timer: 1000,
                });
                dispatch(deleteNote(note));
            }
        } catch (error) {
            console.log(error);
        }
    };

export const startSaving = (active: Note) => async (dispatch: Dispatch) => {
    const newNote = { body: active.body, url: active.url, title: active.title };
    try {
        const resp = await fetchWithToken(`notes/${active.id}`, newNote, "PUT");
        const body = await resp.json();
        if (body.ok) {
            Swal.fire({
                title: "Saved",
                text: "",
                icon: "success",
                timer: 1000,
            });
            dispatch(saveNote(active));
        }
    } catch (error) {
        console.log(error);
    }
};

const saveNote = (active: Note) => ({
    type: types.notesUpdated,
    payload: active,
});

const deleteNote = (active: Note) => ({
    type: types.notesDelete,
    payload: active,
});

export const startUploading =
    (file: File) => async (dispatch: Dispatch, getState: () => RootState) => {
        const { active: activeNote } = getState().notes;
        Swal.fire({
            title: "Uploading...",
            text: "Please wait...",
            allowOutsideClick: false,
            didOpen: () => {
                Swal.showLoading();
            },
        });
        const fileUrl = await fileUpload(file);
        activeNote!.url = fileUrl;
        dispatch<any>(startSaving(activeNote!));
        Swal.close();
    };

export const logoutNotesCleaning = () => ({
    type: types.notesLogoutCleaning,
});
