import moment from "moment";
import { useDispatch } from "react-redux";
import { activeNote } from "../../actions/notes";
import { Note } from "../../interfaces/Note";

export const JournalEntry = ({ id, date, title, body, url }: Note) => {
    const note = { id, date, title, body, url };
    const dispatch = useDispatch();

    const handleActive = () => {
        dispatch(activeNote(note));
    };

    const noteDate = moment(date);
    return (
        <div
            className="journal__entry animate__animated animate__fadeIn animate__faster"
            onClick={handleActive}
        >
            {url && (
                <div
                    className="journal__entry-picture"
                    style={{
                        backgroundSize: "cover",
                        backgroundImage: `url(${url})`,
                    }}
                ></div>
            )}
            <div className="journal__entry-body">
                <p className="journal__entry-title">{title}</p>
                <p className="journal__entry-content">
                    {body.slice(0, 110) + (body.length > 110 ? "..." : "")}
                </p>
                <div className="journal__entry-date-box">
                    <span>{noteDate.format("dddd")}</span>
                    <h4>{noteDate.format("Do")}</h4>
                </div>
            </div>
        </div>
    );
};
