import { SyntheticEvent } from "react";
import { Link } from "react-router-dom";
import { useForm } from "../hooks/useForm";
import { NewUser } from "../../interfaces/NewUser";
import Swal from "sweetalert2";
import { useDispatch } from "react-redux";
import { startRegister } from "../../actions/auth";

const initialState = {
    name: "",
    email: "",
    password: "",
    password2: "",
};

export const RegisterScreen = () => {
    const dispatch = useDispatch();
    const { values, handleInputChange } = useForm<NewUser>(initialState);

    const { name, email, password, password2 } = values;

    const handleSubmit = (e: SyntheticEvent) => {
        e.preventDefault();
        if (!name) {
            return Swal.fire("Error", "Name can not be empty", "error");
        }
        if (!email) {
            return Swal.fire("Error", "Invalid email", "error");
        }
        if (password.length < 6) {
            return Swal.fire(
                "Error",
                "Password must contain at least 6 characters",
                "error"
            );
        }
        if (password !== password2) {
            return Swal.fire("Error", "Passwords are different", "error");
        }

        dispatch<any>(startRegister({ email, password, name }));
    };

    return (
        <>
            <h3 className="auth__title">Register</h3>

            <form
                className="animate__animated animate__fadeIn animate__faster"
                onSubmit={handleSubmit}
            >
                {/* {msgError && (
            <div className="auth__alert-error">{msgError}</div>
        )} */}

                <input
                    className="auth__input"
                    type="text"
                    placeholder="name"
                    name="name"
                    autoComplete="off"
                    value={name}
                    onChange={handleInputChange}
                />
                <input
                    className="auth__input"
                    type="text"
                    placeholder="email"
                    name="email"
                    autoComplete="off"
                    value={email}
                    onChange={handleInputChange}
                />
                <input
                    className="auth__input"
                    type="password"
                    placeholder="Password"
                    name="password"
                    value={password}
                    onChange={handleInputChange}
                />

                <input
                    className="auth__input"
                    type="password"
                    placeholder="Confirm your password"
                    name="password2"
                    value={password2}
                    onChange={handleInputChange}
                />
                <button type="submit" className="btn btn-primary btn-block">
                    Register
                </button>

                {/* <div className="auth__social-networks">
                    <p>Login with social networks</p>

                    <div className="google-btn">
                        <div className="google-icon-wrapper">
                            <img
                                className="google-icon"
                                src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg"
                                alt="google button"
                            />
                        </div>
                        <p className="btn-text">
                            <b>Sign in with google</b>
                        </p>
                    </div>
                </div> */}

                <Link className="link" to="/auth/login">
                    Already Registered?
                </Link>
            </form>
        </>
    );
};
