import moment from "moment";
import { ChangeEvent } from "react";
import { useDispatch, useSelector } from "react-redux";
import { startSaving, startUploading } from "../../actions/notes";
import { RootState } from "../../store/store";

export const NotesAppBar = () => {
    const dispatch = useDispatch();
    const { active } = useSelector((state: RootState) => state.notes);
    const noteDate = moment(active.date);
    const handleSave = () => {
        dispatch<any>(startSaving(active!));
    };
    const handlePictureUpload = () => {
        document.querySelector<HTMLElement>("#fileSelector")!.click();
    };

    const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files![0];
        console.log(file);
        if (file) {
            dispatch(startUploading(file) as any);
        }
    };
    return (
        <div className="notes__appbar">
            <span>{noteDate.format("LLL")}</span>
            <input
                type="file"
                style={{ display: "none" }}
                name="file"
                id="fileSelector"
                onChange={handleFileChange}
            />
            <div>
                <button className="btn" onClick={handlePictureUpload}>
                    Picture
                </button>
                <button className="btn" onClick={handleSave}>
                    Save
                </button>
            </div>
        </div>
    );
};
